// contains list of bad words we dont't want the Desktop to render
// these are stored as Base64 so we don't have to see them introduced into the codebase
// if you wish to view the list simply run: "console.log(atob(forbiddenNotes.base64))" in console

let forbiddenNotes = {
  "base64": ""
};

forbiddenNotes.randowFunWord = function randowFunWord() {
  let words = ["daffodil", "chrysanthemum", "waffles", "puppies", "senpai", "kohai", "rainbow", "fluffy", "marklar"];
  // TODO: use bp.random() in order to get seedable randomness
  // this will provide consistent results across all client instances
  var item = words[Math.floor(Math.random() * words.length)];
  return item;
}

forbiddenNotes.containsBadWord = function (str) {
  // Convert input to lowercase for case-insensitive matching
  const lowerStr = str.toLowerCase();
  // Check each forbidden word using word boundaries
  return forbiddenNotes.decoded.some(function (fnote) {
    // Create a regex with word boundaries for whole-word matching
    const regex = new RegExp(`\\b${fnote}\\b`, 'i');
    const found = regex.test(lowerStr);
    return found;
  });
};

forbiddenNotes.filter = function (str) {
  forbiddenNotes.decoded.forEach(function (fnote) {
    str = str.replace(new RegExp("\\b" + fnote + "\\b"), forbiddenNotes.randowFunWord())
    str = str.replace(new RegExp("\\b" + fnote + "\\b", "gi"), forbiddenNotes.randowFunWord())
  });
  return str;
}

forbiddenNotes.decoded = JSON.parse(atob(forbiddenNotes.base64))

export default forbiddenNotes;
